import axios from "axios";

const laravelUrl = "http://api.ipito.local/api";

const postDataService = {
  /**
   *
   *
   *
   *
   *
   *
   *
   *
   *
   *
   * Speichert einen neuen Post
   */
  store: (postData) => {
    // hier definieren wir die laravel route, die wir zum Speichern verwenden mchten (wir geben das in eine Konstante, um den eigentlichen Request leserlicher zu machen)
    // das Muster fr die korrekten URL routes in Laravel fr unsere Route, findet ihr unter https://laravel.com/docs/8.x/controllers#resource-controllers (Tabelle unter "Actions Handled by Resource Controller" - hier steht welcher Pfad zu welcher Action passt. ["photos" ist hier nur ein Beispiel. Bei uns ist es z.Bsp: "posts"])
    const requestUrl = laravelUrl + "/posts";

    // da wir hier nun einen Request an eine api machen, verwenden wir "axios" um die Daten zu holen
    return axios.post(requestUrl, postData).then((res) => {
      console.log(res);
      // wir bekommen hier von axios ein Objekt mit allen mglichen Infos zum Request + den Daten unter "data" zurck. Da uns nur die Daten interessieren, geben wir nur diese weiter.
      return res.data;
    });
  },

  /**
   *
   *
   *
   *
   *
   *
   *
   *
   *
   * Gibt eine Liste mit allen Posts zurck
   */
  index: () => {
    const requestUrl = laravelUrl + "/posts";

    // bitte beachtet hier, dass wir hier "axios.get" statt wie in der Funktion darber "axios.post" verwenden (dadurch wei Laravel, dass wir laut Tabelle unter https://laravel.com/docs/8.x/controllers#resource-controllers die Laravel Aktion "index" aufrufen wollen)
    return axios.get(requestUrl).then((res) => {
      return res.data;
    });
  },

  /**
   *
   *
   *
   *
   *
   *
   *
   *
   *
   *
   * Gibt nur einen spezifischen Post
   */
  find: (postId) => {
    const requestUrl = laravelUrl + "/posts/" + postId;

    return axios.get(requestUrl).then((res) => {
      return res.data;
    });
  },

  /**
   *
   *
   *
   *
   *
   *
   *
   *
   *
   *
   * Update einen speziellen Post
   */
  update: (postId, newData) => {
    const requestUrl = laravelUrl + "/posts/" + postId;

    // bitte beachtet hier, dass wir hier "axios.put" statt "axios.get" verwenden (dadurch wei Laravel, dass wir laut Tabelle unter https://laravel.com/docs/8.x/controllers#resource-controllers die Laravel Aktion "update" aufrufen wollen)
    return axios.put(requestUrl, newData).then((res) => {
      return res.data;
    });
  },

  /**
   *
   *
   *
   *
   *
   *
   *
   *
   *
   *
   * Lscht einen speziellen Post
   */
  delete: (postId) => {
    const requestUrl = laravelUrl + "/posts/" + postId;

    // bitte beachtet hier, dass wir hier "axios.delete" verwenden
    return axios.delete(requestUrl).then((res) => {
      return res.data;
    });
  },
};

export default postDataService;
